.carousel {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}

.panel {
    display: flex;    
    position: relative;
    width: 5em;
    background-position: center;
    background-repeat: no-repeat;
    height: 80vh;
    margin: 10px;
    border-radius: 16px;
    cursor: pointer;
    flex: 0.5;
    transition: flex 0.7s ease-in;
    overflow: hidden;
    
    &__title {
        color: white;
        font-size: 24px;
        position: absolute;
        bottom: 20px;
        left: 20px;
        opacity: 0;

        &--active{ 
            opacity: 1;
            transition: all 0.3s ease-in 0.4s;
        }
    }


    &.active {
        flex: 5;
    }

}

@media (max-width: 480px){
    .carousel {
        width: 100vw;
    }

    .pannel:nth-last-of-type(4),
    .pannel:nth-last-of-type(5) {
        display: none;
    }
}